import React from 'react'
import { fetchUserData } from '../store'

const TestRemove = ({ data }) => {
    return (
        <div>
            {data}
        </div>
    )
}

TestRemove.getInitialProps = async ({ reduxStore }) => {
    let res = await reduxStore.dispatch(fetchUserData())
    console.log(res)
    return {
        data: "random"
    }
}

export default TestRemove